import request from '../../util/request.js'
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			date: currentDate,
			user: {
				birthDate: '',
				email: "",
				gender: 1,
				identityCardNo: "",
				identityCardType: 1,
				mobilePhone: "",
				password: "",
				realname: "",
				username: ""
			},
			items: [{
					value: 'girl',
					name: '女'
				},
				{
					value: 'boy',
					name: '男'
				}
			],
			current: 1,
			cards: [{
					value: 'zid',
					name: '暂住证'
				},
				{
					value: 'id',
					name: '身份证件'
				}
			],
			no: 1
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					this.user.gender = this.current;
					console.log(this.user.gender);
					break;
				}
			}
		},
		cardchange: function(evt) {
			for (let i = 0; i < this.cards.length; i++) {
				if (this.cards[i].value === evt.target.value) {
					this.no = i;
					this.user.identityCardType = this.no;
					console.log(this.user.identityCardType);
					break;
				}
			}
		},
		register: function() {
			this.user.birthDate=this.date;
			console.log(this.user.birthDate);
			console.log(this.user.email);
			console.log(this.user.gender);
			console.log(this.user.username);
			console.log(this.user.password);
			console.log(this.user.mobilePhone);
			console.log(this.user.realname);
			console.log(this.user.identityCardType);
			console.log(this.user.identityCardNo);
			let ins={
				url:'/customer',
				method:'post'
			}
			let data=this.user;
			request.TokenRequest(ins,data).then(res=>{
				uni.showToast({
					title:res.message,
					duration: 2000
				})
			}).catch(error=>{
				console.log(error)
			})
			uni.redirectTo({
				url: "../login/login"
			})
		}
	}
}
